const Diary = () => {
    return (
      <div className="diary-container">
        {diaryEntries.map((entry, index) => (
          <div 
            key={index} 
            className={`diary-entry ${entry.align === 'right' ? 'right' : ''}`}
          >
            <img src={entry.image} alt={`Diary entry ${index + 1}`} className="diary-image" />
            <div className="diary-text">{entry.text}</div>
          </div>
        ))}
      </div>
    );
  };

export default Diary;
